.container {
    text-align: center;
    background: blueviolet;

}

.search-box {
    background: #1b0c1d;
    padding: 10px;
    color: #fff;
}

.filmList {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.filmItem {
    padding: 10px 0;
    border-radius: 5px;
    background: #eee;
    margin: 20px 15px;
    width: 350px;
    transition: all .3s;
}

.filmItem:hover {
    background: #b0cace;
    margin: 15px;
    box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.5);
}

.search-item {
    width: 100%;
    height: 100%;
    justify-content: center;
    align-Items: center;
}

input[type="text"], textarea, select {
    outline: none;
}

input {
    width: 200px;
    height: 45px;
    border-radius: 25px;
    font-size: 16px;
    padding-left: 45px;
    background: #0003;
    color: #fff;
    margin-top: 10px
}

/**! PAGINATION*/
.paginationContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px;
}
.pagination {
    display: flex;
}
.stepBtn {
    padding: 8px 16px;
    border: 1px solid #1b0c1d;
    border-radius: 3px;
    cursor:pointer;
}
.stepBtn:hover{
    background: #1b0c1d;
    color:blueviolet;
}
.active{
    background: #1b0c1d;
    color: #ffffff;
}


.movieContainer{
    text-align: center;
    justify-content: center;
    background-image: linear-gradient( #1b0c1d, blueviolet);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100vh;
    display: flex;
}

.backBtn{
    display: flex;
    justify-content: center;
    align-items: center;
    height:30px;
    width: 100px;
    background: blueviolet;
    border-radius: 30px;
    color: #ffffff;
    box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.5);

}
.card {
    float: left;
    padding: 0 1.7rem;
    width: 50%;
}
.card .wrapper {
    height: 600px;
    position: relative;
    overflow: hidden;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.2);
}
.card .wrapper:hover .data {
    -webkit-transform: translateY(20vh);
            transform: translateY(20vh);
}
.card .data {
    color: #fff;
    position: absolute;
    bottom: 0;
    background: #000a;
    -webkit-transform: translateY(67vh);
            transform: translateY(67vh);
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
    width: -webkit-fill-available;
}
.content{
    height: 500px;
}
.example-2 .header {
    color: #fff;
    padding: 1em;
}
.example-2 .header .date {
    font-size: 12px;
    display: flex;
    justify-content: space-around;
}




